import moment from "moment";

export const contractTemplate = "<h1 class=\"ql-align-center\">CONTRACT DE EXECUTIE LUCRARI</h1><p class=\"ql-align-center\"><strong>Nr. 837 din " + moment(new Date()).format("D.M.YYYY") +"</strong></p><p><br></p><p><br></p><p><strong>1. PARTILE CONTRACTANTE</strong></p><p><br></p><p>Intre:</p><p><strong>SC CONSIG SA</strong> cu sediul in Giurgiu, str. Libertatii, nr. 9, telefon/fax 0246/218340, numar de inmatriculare J52/71/91, cod fiscal RO 1289447, cont IBAN RO85 BTRL RON CRT 02 3093 3302, deschis la Banca Transilvania - Sucursala Giurgiu reprezentant prin ing<strong>. </strong>Dop Virgil – Director General in calitate de beneficiar, </p><p>Si:</p><p><strong>SC FLO MEDIA STAR SRL</strong> cu sediul in mun. Bucuresti, sector 3, strada Racari, nr. 10, bl. 41, sc. 1, ap. 14, nr. de inmatriculare J40/14517/2014, cod unic 33900793, cod IBAN RO29 BTRL RONCRT0280854002 deschis la Banca Transilvania – Sucursala Sector 3 reprezentata prin Sarghie Ana Florina – Administrator in calitate de executant,</p><p>a intervenit prezentul contract la obiectivul: “Parchetul de pe langa Tribunalul Giurgiu” - <strong>Lucrari de refacere a sistemului hidroizolant la acoperisul terasei de peste et. III  </strong></p><p><br></p><p><strong>2. OBIECTUL SI PRETUL CONTRACTULUI</strong></p><p><br></p><p>2.1. Executantul se obliga sa execute lucrari de manopera de refacere a hidroizolatiei la acoperisul mentionat mai sus<strong>,</strong> in conformitate cu obligatiile asumate in prezentul contract. </p><p>2.2. Locatia unde se vor efectua lucrarile stabilite in prezentul contract va fi: Parchetul de pe langa tribunalul Giurgiu, bdul. Mihai Viteazul, judetul Giurgiu.</p><p>2.3. Pretul contractului este de 9 lei/mp/strat. Pretul final al contractului se va stabili dupa ce se vor face masuratorile cantitative. </p><p>2.4. Lucrarile care se vor executa (orizontal si vertical) sunt urmatoarele: total 417 mp in doua straturi de membrane bituminoase cu amorsa, puse la dispozitie de catre beneficiar. </p><p><br></p><p><strong>3. DURATA CONTRACTULUI</strong></p><p><br></p><p>3.1. Durata contractului – pana la receptia finala a lucrarilor (30.12.2021).</p><p>3.2. Executarea contractului incepe in data de 17.10.2018.</p><p>3.3. Prezentul contract inceteaza sa produca efecte la terminarea perioadei de garantie (3 ani) de la receptia la terminarea lucrarilor.</p><p><br></p><p><strong>4. OBLIGATIILE EXECUTANTULUI</strong></p><p><br></p><p>4.1- (1) Executantul are obligatia de a executa si a finaliza lucrarile in termenul ofertat precum si a remedia viciile ascunse, cu atentia si promptitudinea cuvenita, in concordanta cu obligatiile asumate in contract.</p><p>(2) Executantul are obligatia de a asigura forta de munca si sculele necesare, fie de natura provizorie, fie definitive, cerute pentru contract. Buteliile necesare in procesul de lucru vor fi asigurate de catre executant.</p><p>(3) Executantul are obligatia de a notifica promt beneficiarului despre toate erorile, omisiunile, viciile, sau altele asemenea descoperite de el in proiect sau in caietul de sarcini pe durata indeplinirii contractului.</p><p>4.2- Executantul este pe deplin responsabil pentru conformitatea, stabilitatea si siguranta tuturor operatiunilor executate pe santier si pentru procedeele de executie utilizate, cu respectarea prevederilor si reglementarilor legii privind calitatea in constructii.</p><p>4.3- Executantul are obligatia de a respecta si executa dispozitiile beneficiarului in orice problema, mentionata sau nu in contract, referitoare la lucrare. In cazul in care executantul considera ca dispozitiile beneficiarului sunt nejustificate sau inoportune, acesta are dreptul de a ridica obiectii, in scris, fara ca obiectivele respective sa il absolve de obligatia de a executa dispozitiile primite, cu exceptia cazului in care acestea contravin prevederilor legale.</p><p>4.4- Pe parcursul executiei lucrarilor si remedierii viciilor ascunse, executantul are obligatia:</p><p>i) de a lua toate masurile pentru asigurarea tuturor persoanelor a caror prezenta pe santier este autorizata si de a mentine santierul (atat timp cat este sub controlul sau) si lucarile (atat timp cat acestea nu sunt finalizate si ocupate de catre achizitor) in starea de ordine necesara evitarii oricarui pericol pentru respectivele personae.</p><p>4.5- Executantul este responsabil pentru mentinerea in buna stare a lucrarilor, materialelor, echipamentelor si instalatiilor care urmeaza a fi puse in opera de la data semnarii contractului pana la data procesului verbal de receptie a lucrarilor.</p><p>4.6- Pe parcursul executiei lucrarilor si a remedierilor viciilor ascunse, executantul are obligatia, in masura permisa de respectarea prevederilor contractului, de a nu stanjeni inutil sau in mod abuziv:</p><p>a) confortul riveranilor</p><p>b) caile de acces, prin folosirea si ocuparea drumurilor si cailor publice sau private care deservesc proprietatile aflate in posesia antreprenorului sau a oricarei alte personae.</p><p>(2) Executantul va despagubi beneficiarul impotriva tuturor reclamatiilor, actiunilor in justitie, daunelor-interese, costurilor, taxelor si cheltuielilor, indiferent de natura lor rezultand din sau in legatura cu obligatia prevazuta la alin(1), pentru care responsabilitatea revine executantului.</p><p>4.7- Executantul se obliga de a despagubi beneficiarul impotriva oricaror:</p><p>i) reclamatii si actiuni in justitie, ce rezulta din incalcarea unor drepturi de proprietate intelectuala (brevet, nume, marci inregistrate etc), legate de echipamentele, materialele, instalatiile sau utilajele folosite pentru sau in legatura cu executia lucarilor.</p><p>ii) daune- interese, costuri, taxe si cheltuieli de orice natura, aferente; cu exceptia situatiei in care o astfel de incalcare rezulta din respectarea proiectului sau caietului de sarcini intocmit de catre antreprenor.</p><p>4.8- Executantul va asigura si va plati efectuarea tuturor probelor de laborator impuse prin caietul de sarcini.</p><p>4.9 - Raspunde de asigurarea conditiilor de respectare a protectiei muncii si de eventualele accidente de munca si/sau imbolnavirii profesionale; in acest sens s-a incheiat Conventia de protectia muncii, anexa nr. 1 la acest contract </p><p>4.10 – Hidroizolatia va fi executata conform cerintelor beneficiarului (planuri de executie), tinand cont si de cerintele din caietul de sarcini.</p><p><br></p><p><strong>5. OBLIGATIILE BENEFICIARULUI</strong></p><p><br></p><p>5.1- (1) Beneficiarul are obligatia de a pune la dispozitia executantului, fara plata, planurile de executie si caietul de sarcini.</p><p>(2) Costurile pentru consumul de utilitati precum si cel al contoarelor sau altor aparate de masurat se suporta de catre beneficiar.</p><p>5.2- Beneficiarul are obligatia de a examina si masura lucrarile care devin ascunse in cel mult 5 zile de la notificarea executantului.</p><p>5.3- Beneficiarul achita executantului contravaloarea prestatiei in termenele stabilite prin facturi si prezentul contract.</p><p><br></p><p><strong>6. INTARZIEREA SI SISTAREA LUCRARILOR</strong></p><p><br></p><p>6.1. in cazul in care, din vina sa exclusiva, executantul nu reuSeSte sa-Si indeplineasca obligatiile </p><p>asumate prin contract, atunci beneficiarul este indreptatit de a deduce din pretul contractului, ca </p><p>penalitati, o suma echivalenta cu o cota procentuala de 0,1% pe zi din pretul contractului dar nu mai mult </p><p>de valoarea contractului.</p><p>6.2 in cazul in care beneficiarul nu onoreaza facturile in termen de 5 de zile lucratoare de la expirarea </p><p>perioadei convenite (proces verbal de receptie la terminarea lucrarilor), atunci acesta are obligatia de a plati, ca penalitati, o suma echivalenta cu o cota procentuala de 0.1% pe zi din valoarea datorata, dar nu mai mult de valoarea contractului.</p><p>6.3 Nerespectarea obligatiilor asumate prin prezentul contract de catre una dintre parti, in mod </p><p>culpabil Si repetat, da dreptul partii lezate de a considera contractul de drept reziliat Si de a pretinde </p><p>plata de daune-interese.</p><p><br></p><p><strong>7. GARANTIA LUCRARILOR</strong></p><p><br></p><p>7.1 – Perioada de garantie acordata lucrarilor este de 36 de luni si decurge de la data receptiei la terminarea lucrarilor si pana la receptia finala.</p><p>7.2 - Pentru lucrarile pe care le va efectua executantul, nu se vor percepe garantii financiare. Acesta se obliga sa repare toate neconformitatile aparute din vina sa pe timpul desfasurarii contractului, in maximum 3 zile lucratoare, de la constatare. </p><p>7.3 - In cazul in care executantul nu va repara neconformitatile asa cum s-a obligat, beneficiarul va putea angaja o alta societate, iar plata solicitata de catre aceasta va fi refacturata executantului, cu obligatia achitarii. In caz contrar beneficiarul va putea solicita daune-interese. </p><p><br></p><p><strong>8.MODALITATI DE PLATA</strong></p><p><br></p><p>8.1. Plata se va efectua cu ordin de plata sau cu chitanta in termen de 3 zile de la data emiterii facturii.</p><p>8.2. Plata se va efectua numai daca facturile vor fi insotite de procesul verbal de receptie a lucrarii si de documentele care atesta calitatea materialelor si a lucrarilor.</p><p>8.3. Ajustarea contractului: pretul nu se ajusteaza</p><p><br></p><p><strong>9. EXECUTIA LUCRARILOR</strong></p><p><br></p><p>9.1- Executantul are obligatia de a incepe lucrarile odata cu semnarea prezentului.</p><p>9.2- Beneficiarul are dreptul de a supraveghea desfasurarea executiei lucrarilor.</p><p><br></p><p><strong>10. FORTA MAJORA</strong></p><p><br></p><p>10.1. Forta majora exonereaza partile contractante de indeplinirea obligatiilor asumate prin prezentul contract, pe toata perioda in care actioneaza acesta.</p><p>10.2 Partea contractanta care invoca forta majora are obligatia de a notifica celeilalte parti, imediat si in mod complet, producerea acestuia si de a lua masuri care ii stau la dispozitie, in vederea limitarii consecintelor.</p><p><br></p><p><strong>11. SOLUTIONAREA LITIGIILOR</strong></p><p><br></p><p>11.1- Beneficiarul si executantul vor face toate eforturile pentru a rezolva pe cale amiabila, prin tratative directe, orice neantelegere sau disputa care se poate ivi intre ei in cadrul sau in legatura cu indeplinirea contractului.</p><p>11.2-Daca, dupa 15 zile de la inceperea acestor tratative neoficilae, beneficiarul si executantul nu reusesc sa rezolve in mod amiabil o divergenta contractuala, fiecare poate solicita ca disputa sa se solutioneze de catre Judecatoria Giurgiu.</p><p><br></p><p><strong>12. LIMBA CARE GUVERNEAZA CONTRACTUL</strong></p><p><br></p><p>12.1- Limba care guverneaza contractul este limba romana</p><p><br></p><p><strong>13. COMUNICARI</strong></p><p><br></p><p>13.1- (1) Orice comunicare intre parti, referitoare la indeplinirea prezentului contract, trebuie sa fie transmisa in scris</p><p>(2) Orice document scris trebuie inregistrat atat in momentul transmiterii cat si in momentul primirii.</p><p>13.2- Comunicarile intre parti se pot face si prin telefon, fax sau email cu conditia confirmarii in scris a primirii comunicarii.</p><p><br></p><p><strong>14. LEGEA APLICABILA CONTRACTULUI</strong></p><p><br></p><p>14.1- Contractul va fi interpretat conform legii din Romania.</p><p><br></p><p><strong>15. DOCUMENTELE CONTRACTULUI</strong></p><p><br></p><p>15.1. Anexa nr. 01 – Conventie de protectia muncii.</p><p><br></p><p><strong>16. DISPOZITII FINALE</strong></p><p><br></p><p>16.1 Modificarea prezentului contract se face numai prin act aditional incheiat intre partile contractante.</p><p>16.2 Prezentul contract, impreuna cu anexele sale care fac parte integranta din cuprinsul sau, reprezinta vointa partilor si inlatura orice alta intelegere verbala dintre acestea, anterioara sau ulterioara incheierii lui.</p><p>16.3 Prezentul contract s-a incheiat astazi 16.10.2018 in 2 exemplare originale, din care cate unul pentru fiecare parte.</p><p><br></p><p><strong>EXECUTANT,  </strong>\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t <strong>BENEFICIAR,</strong></p><p><strong>SC FLO MEDIA STAR SRL </strong>\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<strong>SC CONSIG SA </strong></p><p><strong>SARGHIE ANA FLORINA DOP VIRGIL</strong></p>";


