import moment from "moment";

export const anexaTemplate = "<p><br></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p><h1 class=\"ql-align-center\"><strong>ANEXA&nbsp;nr. 02 la</strong></h1><h2 class=\"ql-align-center\"><strong>Contractul de executie lucrari nr. 837 din " + moment(new Date()).format("D.M.YYYY") +"</strong></h2><h3 class=\"ql-align-center\"><strong>CONVENTIE DE PROTECTIA MUNCII SI PSI</strong></h3><p class=\"ql-align-center\"><br></p><p class=\"ql-align-center\"><br></p><p class=\"ql-align-center\"><br></p><p>Incheiata azi " + moment(new Date()).format("D.M.YYYY") +" intre <strong>SC CONSIG SA</strong> in calitate de beneficiar reprezentata prin Dop Virgil – director general si <strong>SC FLO MEDIA STAR&nbsp;SRL</strong> in calitate de executant reprezentata de Sarghie Ana Florina - administrator. In scopul prevenirii accidentelor de munca si imbolnavirilor profesionale, s-a incheiat prezenta conventie, prin care se stabilesc obligatiile si raspunderile reciproce, dupa cum urmeaza:</p><p>\t</p><p>\t<strong>A</strong>:&nbsp;&nbsp;<strong><u>SARCINILE PRESTATORULUI DE SERVICII</u></strong></p><p>\t</p><p><strong>1.</strong> Executantul isi va dota personalul cu echipamente tehnice de protectie si de lucru corespunzatoare.</p><p><strong>2.</strong> Executantul isi va instrui personalul cu normele de protectia muncii si PSI pe care sa le respecte la lucrarea respectiva.</p><p><strong>3.</strong> Personalul de executie trebuie sa fie autorizat conform reglementarilor in vigoare (sefi de echipa, legatori de sarcina, electricieni, etc.).</p><p><strong>4.</strong> Sa respecte caile de acces stabilite de beneficiar si sa nu se deplaseze in afara zonei de lucru.</p><p><strong>5.</strong> Sa nu blocheze caile de acces in incinta beneficiarului.</p><p><strong>6.</strong> Seful de echipa este direct raspunzator de asigurarea curateneiei la locul de munca, inainte de retragerea zilnica a echipei de la lucru.</p><p><strong>7.</strong> Sa evacueze periodic deseurile rezultate de la lucrare cu mijloace si forte proprii.</p><p><strong>8.</strong> Fumatul este permis doar in locurile special amenajate.</p><p><strong>9.</strong> In cazul lucrarilor ce se executa pe aceeasi verticala, executantul isi va lua toate masurile pentru a elimina posibilitatile de accidentare a personalului ce lucreaza la cotele inferioare. De asemenea este obligat sa respecte dispozitiile celor ce lucreaza la cotele superioare.</p><p><strong>10.</strong> Este interzis accesul executantului in instalatiile electrice ale beneficiarului. Modul si locul de racordare se va stabili impreuna cu sectia electrica a beneficiarului. Executantul raspunde de instalatiile electrice pe care le utilizeaza de la punctul de racord pana la consumatorul electric (inclusiv).</p><p><strong>11.</strong> Executantul va controla periodic modul de respectare a normelor de&nbsp;protectie a muncii de catre personalul din subordine si va lua masuri operative de eliminare a eventualelor deficiente sesizate de beneficiar.</p><p><strong>12.</strong> Executantul se va asigura ca echipamentele tehnice din dotare sa fie in stare de functionare si conform cerintelor de securitate.</p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p>\t<strong>B.&nbsp;&nbsp;<u>SARCINILE BENEFICIARULUI</u></strong></p><p><br></p><p><strong>1.&nbsp;</strong>&nbsp;Stabileste si aduce la cunostinta personalului prestatorului caile de acces pe teritoriul unitatii, in cadrul zonelor de lucru.</p><p><strong>2.</strong>&nbsp;&nbsp;Aduce la cunostinta personalului prestatorului asupra zonelor sau locurilor de munca sau instalatiilor si utilajelor cu grad ridicat de pericol in exploatare.</p><p><strong>3.</strong>&nbsp;&nbsp;Controleaza periodic modul de executie al lucrarilor si respectarea masurilor de protectie a muncii dispuse in documentele de lucru.</p><p><strong>4.</strong>&nbsp;&nbsp;In cazul in care beneficiarul constata ca in zonele in care lucreaza echipele executantului nu se respecta prevederile NPM, PSI si ale prezentei conventii, au dreptul sa sisteze lucrarile pana la remedierea deficientelor in scopul prevenirii accidentelor de munca sau incidentelor periculoase.</p><p><br></p><p>\t<strong>C.&nbsp;&nbsp;<u>SARCINI COMUNE</u></strong></p><p><br></p><p>In cazul producerii unor accidente de munca in care este implicat atat personalul executantului cat si al beneficiarului se stabilesc urmatoarele:</p><p> <strong>1.</strong>&nbsp;&nbsp;Cercetarea cauzelor si imprejurarilor producerii accidentelor se va face in comun de catre comisii numite prin decizii, conform prevederilor in vigoare.</p><p> <strong>2.</strong>&nbsp;&nbsp;Accidentele de munca se vor inregistra de catre unitatea al carei personal </p><p>se face vinovat de producerea lui.</p><p> <strong>3.</strong>&nbsp;&nbsp;Accidentul de munca de traseu si accidentul de munca de circulatie se </p><p>inregistreaza de catre persoana juridica la care este angajata victima.</p><p> <strong>4.</strong>&nbsp;&nbsp;Accidentele de munca avand cauze pur tehnice, fara vina de personal, se vor inregistra de catre unitatea la care este angajat salariatul.</p><p> <strong>5.</strong>&nbsp;&nbsp;Accidentele care se produc in afara zonei de lucru sau a cailor de acces, se inregistreaza de catre unitatea la care este angajat salariatul.</p><p> <strong>6.</strong>&nbsp;&nbsp;In cazul in care apar situatii deosebite sau divergente intre parti, inregistrarea accidentului se va stabili de catre ITM Giurgiu</p><p>\t</p><p>Aceasta conventie face parte din Contractul nr. <strong>837 din " + moment(new Date()).format("D.M.YYYY") +"</strong> incheiat intre cele doua societati.</p><p><br></p><p><strong>EXECUTANT,&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                                                     BENEFICIAR,</strong></p><p><strong>SC FLO MEDIA STAR&nbsp;SRL&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                                                    &nbsp;SC CONSIG SA </strong></p><p><strong>SARGHIE ANA FLORINA&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                                                    DOP VIRGIL</strong></p><p><br></p><p><br></p>";


